<template>
    <div class="search_list">
      <div class="search">
        <form action="/">
          <van-search
            v-model="value"
            placeholder="搜索"
            show-action
            @search="getSearchList"
          />
        </form>
      </div>
      <div class="search-list" v-if="searchListCat.length>=1 || searchListGoods.length>=1">
        <div v-for="item in searchListCat" :key="item.code" class="search-item" @click="toLanmu(item.id)">
          <span class="iconfont icon-fenlei2"></span>
          <div class="danyi">{{item.name}}</div>
        </div>
        <div v-for="item in searchListGoods" :key="item.code" class="search-item" @click="toDetail(item.id)">
          <span class="iconfont icon-shangpin"></span>
          <div class="danyi">{{item.goods_name}}</div>
        </div>
      </div>
    </div>
</template>

<script>
import img from "./../assets/img/timg.jpg"
import { apiGet } from '../common/js/api';
    export default {
        name: "search_list",
      data(){
        return{
          value:"",
          searchListCat:[

          ],
          searchListGoods:[]
        }
      },
      watch:{
        'value':function(newVal,oldVal){
          if(newVal!==oldVal){
            this.getSearchList()
          }
        }
      },
      created(){

      },
      methods:{
        getSearchList(){
          console.log(this.value);
          apiGet("/goods/goods-search",{
            key:this.value,
            page:1,
            size:100
          }).then(res=>{
            this.searchListCat = res.data.data.cat;
            console.log(res.data)
            this.searchListGoods = res.data.data.goods;
            console.log(this.searchListCat.length,this.searchListGoods.length)
          })
        },
        //跳转商城列表
        toDetail(value){
          this.$router.push({
            name:'spXiangqin',
            params:{
              id:value,
            }
          })
        },
        toLanmu(value){
          this.$router.push({
            name:'scLanmu',
            query:{
              cate_id:value,
            }
          })
        }
      }
    }
</script>

<style lang="scss">
  .search_list{
    width:100%;
    height: 100%;
    background: #E6E9ED;
    display: flex;
    flex-direction: column;
  }
  .van-search .van-cell{
    height: 0.42rem;
  }
  .van-cell__left-icon{
    line-height: 0.35rem;
  }
  .van-search__action{
    display: none;
  }
  .search{
    width: 100%;
    height:.76rem;
    background: #E6E9ED;
    .van-search{
      width: 100%;
      padding: 0.17rem 0.13rem;
      background: #E6E9ED !important;
      input{
        line-height: 0.4rem !important;
      }
    }
  }
  .search-list{
    background: #fff;
    width:100%;
    padding:0 .13rem 0rem .13rem;
    .search-item{
      padding:0 .06rem;
      height: .5rem;
      border-bottom: .01rem solid #F5F5F5;
      line-height: .5rem;
      font-size: .17rem;
      color: #333;
        display: flex;
          div{
            display: inline-block;
            height: 100%;
          }
      span{
        margin-right: 0.3rem;
      }

    }

  }

</style>



